<template>
    <header class="header">
        <nav>
            <div class="logo">
                <img src="../../assets/images/logo.png" :alt="sitename">
            </div>
            <div class="menu">
                <router-link to="/">首页</router-link>
                <router-link to="/class">课程与费用</router-link>
                <router-link to="/family">锐昂大家庭</router-link>
                <router-link to="/about">锐昂新闻</router-link>
                <router-link to="/about">关于锐昂</router-link>
            </div>
            <div class="service">
                <div class="icon">
                    <img src="../../assets/images/tel.png" alt="">
                </div>
                <span>客服电话：13996103243</span>
            </div>
        </nav>
    </header>
</template>

<script>
    export default {
        props:['sitename']
    }
</script>

<style lang="less" scoped>
    .header{
        width: 100%;
        height: 69px;
        background: #000;
        box-sizing: border-box;
        padding: 5px 0;
        nav{
            padding: 0;
            width: 1200px;
            margin: 0 auto;
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 15px;
            .logo{
                img{
                    max-height: 100%;
                }
            }
            .menu{
                color: #fff;
                a{
                    margin:0 25px; 
                }
            }
            .service{
                display: flex;
                align-items: center;
                span{
                    margin-left: 10px;
                    color: #f8db97;
                }
            }
        }
    }
</style>


