import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/view/Index'
import Family from '@/view/Family'
import Class from '@/view/Class'
import About from '@/view/About'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'Index',
            component: Index
        },
        {
            path: '/class',
            name: 'Class',
            component: Class
        },
        {
            path: '/family',
            name: 'Family',
            component: Family
        },
        {
            path: '/about',
            name: 'About',
            component: About
        }
    ]
})


