<template>
    <div class="page-about">
        <v-header :sitename="sitename"/>
        <div class="banner">
            <img src="../assets/images/about-banner.jpg" alt="课程与费用">
        </div>
        <article class="content">
            <h2>锐昂拳击俱乐部</h2>
            <p>
                重庆锐昂拳击俱乐部位于大坪龙湖时代天街16号楼13层，主要经营面积226平方米，是重庆第一家也是目前唯一一家专业美式风格拳击俱乐部，成立于2014年9月。
            </p>
            <p>
                <img src="../assets/images/about-img.jpg" alt="">
            </p>
            <p>地址：重庆市渝中区大坪时代天街C馆16号写字楼13楼     联系人：曹老师 13996103243</p>
            
            <p><img src="../assets/images/weixin.jpg" alt=""></p>
        </article>
    </div>
</template>

<script>
    import vHeader from './components/Header'
    export default {
        data() {
            return {
                sitename: '锐昂拳击俱乐部'
            }
        },
        components:{
            vHeader
        }
    }
</script>

<style scoped lang="less">
    .page-about{
        .banner{
            img{
                max-width: 100%;
                display: block;
            }
        }
        .content{
            width: 1200px;
            margin: 0 auto;
            padding-top: 70px;
            color: #484848;
            h2{
                text-align: center;
            }
            p{
                line-height: 20px;
                text-align: center;
                mark{
                    background: none;
                    color: #ff0000;
                }
                img{
                    display: block;
                    margin: 0 auto;
                }
            }
        }
    }
</style>


