<template>
    <div class="page-fml">
        <v-header :sitename="sitename"/>
        <div class="banner">
            <img src="../assets/images/fml-banner.jpg" alt="课程与费用">
        </div>
        <article class="content">
            <h2>锐昂拳击俱乐部</h2>
            <p>
                我们的团队中尤为重要,一个大家庭,各自有着不同的性格,不同的优点,把各自的长 处互补的发挥,那么我们的团队将会是无人可比的 “要想公道,打个颠倒”。
            </p>
            <p>
                <img src="../assets/images/fml-img.jpg" alt="">
            </p>
        </article>
    </div>
</template>

<script>
    import vHeader from './components/Header'
    export default {
        data() {
            return {
                sitename: '锐昂拳击俱乐部'
            }
        },
        components:{
            vHeader
        }
    }
</script>

<style scoped lang="less">
    .page-fml{
        .banner{
            img{
                max-width: 100%;
                display: block;
            }
        }
        .content{
            width: 1200px;
            margin: 0 auto;
            padding-top: 70px;
            color: #484848;
            h2{
                text-align: center;
            }
            p{
                line-height: 20px;
                mark{
                    background: none;
                    color: #ff0000;
                }
                img{
                    display: block;
                    margin: 0 auto;
                }
            }
        }
    }
</style>


