<template>
    <div class="page-index">
        <index-header :sitename="sitename" />
        <index-banner/>
        <index-footer/>
    </div>
</template>

<script>
    import IndexHeader from './components/IndexHeader'
    import IndexBanner from './components/IndexBanner'
    import IndexFooter from './components/IndexFooter'
    export default {
        data() {
            return {
            sitename: '锐昂拳击俱乐部'
            }
        },
        components:{
            IndexHeader,
            IndexBanner,
            IndexFooter
        }
    }
</script>

