<template>
    <div class="page-class">
        <v-header :sitename="sitename"/>
        <div class="banner">
            <img src="../assets/images/class-banner.jpg" alt="课程与费用">
        </div>
        <article class="content">
            <h2>课程与价格</h2>
            <p>
                包括：拳击专业训练、拳击私人训练、拳击女子防身术、拳击减重训练、拳击青少年假期训练、体能综合素质训练、户外综合素质训练、“明星课程”跟着“欧弟”练马甲线（专项塑形）。配备专业私人体能训练师，为您制定科学有效的训练计划。俱乐部销售nike、adidas、九日山等拳击品牌正品装备，为您提供一站式的服务。俱乐部提供免费淋浴；免费wifi；免费公共拳套、护手布等拳击装备。储物柜需租赁。上课时间固定。上课期间才营业。除每周三休息，每天下午3:00-4:30；晚上7:30-9:00.
            </p>
            <p>
            <mark>【年卡】原价6988元，现活动价3588元。</mark>
             会员年卡可停卡一次，最长停三个月。使用半年以上期限可转让。课程不限次数。
                
            </p>
            <p>
             <mark>【半年卡】原价3988元，现活动价2588。</mark>课程不限次数。
            </p>
            <p><img src="../assets/images/class-1.jpg" alt=""></p>
            <p><mark>【月卡】700元。</mark>课程不限次数。</p>
            <p> <mark>【次卡】100元/节，15节课起售。</mark>三个月使用期限。</p>
            <p><img src="../assets/images/class-2.jpg" alt=""></p>
            <p>【拳击精品体验课】周二、周天晚7:30-9:00；周六下午3:00-4:30.需提前一天预约。30元一人，90分钟。一节课不超过8人。</p>
            <p>
需准备：鞋底洁净的运动鞋。（入场需换鞋底干净的鞋子，尽量选择软底、轻薄的运动鞋。）穿运动装即可。提前10分钟到即可。</p>

        </article>
    </div>
</template>

<script>
    import vHeader from './components/Header'
    export default {
        data() {
            return {
                sitename: '锐昂拳击俱乐部'
            }
        },
        components:{
            vHeader
        }
    }
</script>

<style scoped lang="less">
    .page-class{
        .banner{
            img{
                max-width: 100%;
                display: block;
            }
        }
        .content{
            width: 1200px;
            margin: 0 auto;
            padding-top: 70px;
            color: #484848;
            h2{
                text-align: center;
            }
            p{
                line-height: 20px;
                mark{
                    background: none;
                    color: #ff0000;
                }
                img{
                    display: block;
                    margin: 0 auto;
                }
            }
        }
    }
</style>


